.article-block {
  font-size: 17px;
  line-height: 29px;
  padding-left: 20px;
  padding-right: 20px;
  background-color: #fff;
  padding-bottom: 25px;
  border-radius: 8px;
  margin-bottom: 15px;
}
.article-block .container {
  background-color: #fff;
  padding-bottom: 40px;
}
.article-block__image-wrapper {
  width: 100%;
}
.article-block__image-wrapper--no-height {
  height: auto;
  background: #FBFBFB;
}
.article-block__image-title {
  margin-top: 8px;
  font-size: 14px;
  line-height: 19px;
  color: #000000;
}
@media (max-width: 768px) {
  .article-block__image-title {
    margin-top: 5px;
  }
}
@media (max-width: 767px) {
  .article-block__image-title {
    font-size: 14px;
  }
}
.article-block__container {
  width: 896px;
  margin: 0 auto;
}
.article-block__video {
  margin: 0 50px;
}
.article-block__text {
  margin-right: 0;
  margin-left: 0;
}
.article-block__author {
  margin: 20px 0 20px 0;
}
.article-block__author-link {
  line-height: 1.2;
  letter-spacing: 0.02em;
  color: #B30000;
  text-decoration: none;
}
.article-block__author-link:hover {
  text-decoration: underline;
}

@media (max-width: 1024px) {
  .article-block {
    font-size: 15px;
    line-height: 24px;
    padding-left: 16px;
    padding-right: 16px;
    padding-bottom: 12px;
  }
  .article-block__container {
    width: 700px;
  }
  .article-block__image-wrapper {
    margin-bottom: 20px;
  }
  .article-block__author {
    margin: 16px 0 0;
  }
}
@media (max-width: 767px) {
  .article-block {
    font-size: 18px;
    line-height: 30px;
    margin: 0 -16px;
    border-radius: 0;
    padding-bottom: 25px;
  }
  .article-block__container {
    width: 100%;
  }
  .article-block__video {
    margin: 0;
  }
  .article-block__author {
    margin: 8px 0 0;
  }
}